.slide {
  display: flex;
  border-radius: 24px;
  overflow: hidden;
  justify-content: center;
  position: relative;
  @include tablet {
    min-width: 480px;
    min-height: 320px;
  }

  &__img-wrapper {
    overflow: hidden;
    position: relative;
    &--befor {
      border-radius: 24px 0 0 24px;
    }
    &--after {
      border-radius: 0 24px 24px 0;
    }
  }
  &__img {
    @include tablet {
      width: 240px;
      height: 320px;
    }
  }
  &__before-box {
    position: absolute;
    z-index: 5;
    bottom: 8px;
    left: 8px;
    background-color: var(--main-text-color);
    border-radius: 24px;
    padding: 4px;
  }
  &__after-box {
    position: absolute;
    z-index: 5;
    bottom: 8px;
    right: 8px;
    background-color: var(--main-text-color);
    border-radius: 24px;
    padding: 4px;
  }
  &__text {
    @include inter-font(500, var(--bcg-color));
    font-size: 12px;
    line-height: 120%;
  }
}

